import React, { Component } from 'react'
import { toyService } from '../services/toyService'
import { Link } from 'react-router-dom';
import { ReviewAdd } from '../cmps/ReviewAdd'
import { loadReviews } from '../store/actions/reviewActions';
import { connect } from 'react-redux';
import { ReviewList } from '../cmps/ReviewList';
import ChatRoom from '../cmps/ChatRoom';


class _ToyDetails extends Component {
    state = {
        toy: null,
        loggedinUser: ''
    }

    componentDidMount() {
        this.loadToy()
        this.loadReviews()
    }

    loadReviews() {
        const id = this.props.match.params.toyId
        this.props.loadReviews({ toyId: id })
    }

    async loadToy() {
        const id = this.props.match.params.toyId
        const toy = await toyService.getById(id)
        this.setState({ toy })
    }





    render() {
        const { toy } = this.state
        const loggedinUser = this.props.loggedinUser
        if (!toy) return <div>Loading...</div>
        return (
            <div className="toy-details flex column justufy-center align-center">
                <div className="chat-details-container flex space-around">
                <img src={toy.imgUrl ? toy.imgUrl : require(`../assets/img/default.jpg`)} alt="" />
                    <div className="details-container flex column justify-center align-center">
                        <h1>name: {toy.name}</h1>
                        <h1>price: ${toy.price}</h1>
                        <h1>type: {toy.type}</h1>
                        <h1>in stock? {toy.inStock ? 'yes' : 'no'}</h1>
                    </div>
                    {loggedinUser && <ChatRoom toyId={toy._id} username={this.props.loggedinUser.username} />}
                </div>
                <ReviewList reviews={this.props.reviews} />
                <ReviewAdd toyId={toy._id} />
                <Link to="/toy" >Back to list</Link>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        reviews: state.reviewReducer.reviews,
        loggedinUser: state.userReducer.loggedinUser
    }
}

const mapDispatchToProps = {
    loadReviews
}

export const ToyDetails = connect(mapStateToProps, mapDispatchToProps)(_ToyDetails)

