import { connect } from 'react-redux'

import React, { Component } from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { Button, TextField } from '@material-ui/core';

import { loadToys, addToy, editToy } from '../store/actions/toyActions'
import { toyService } from '../services/toyService';
import { cloudinaryService } from '../services/cloudinaryService';



const TextFieldOutlined = (props) => <TextField variant="outlined" {...props} label={props.name} />


export class _ToyEdit extends Component {

    state = {
        toy: null
    }


    componentDidMount() {
        const { toyId } = this.props.match.params
        if (toyId) {
            toyService.getById(toyId)
                .then(toy => { this.setState({ toy }) })
        }
        else {
            this.setState({
                toy: {
                    name: '',
                    price: '',
                    type: ''
                }
            })

        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.match.params.toyId !== this.props.match.params.toyId) return toyService.getById(this.props.match.params.toyId).then(toy => { this.setState({ toy }) })
    }

    async onUpdateImg(ev) {
        const img = await cloudinaryService.uploadImg(ev)
        let updatedToy = { ...this.state.toy }
        updatedToy.imgUrl = img.secure_url
        this.setState({ toy: updatedToy }, () => this.props.editToy(updatedToy))
    }

    render() {
        const toy = this.state.toy
        if (!toy) return <h1>loading</h1>
        return (<div>

            <Formik
                initialValues={{ name: toy.name, price: toy.price, type: toy.type }}

                validate={formValues => {
                    // const errors = {};
                    // if (!formValues.myEmail) {
                    //     errors.myEmail = 'Required';
                    // } else if (
                    //     !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(formValues.myEmail)
                    // ) {
                    //     errors.myEmail = 'Invalid email address';
                    // }
                    // if (!formValues.password) errors.password = 'Password is required'
                    // return errors;
                }}

                onSubmit={(values, { setSubmitting }) => {
                    this.setState({ toy: { ...toy, ...values } }, () => {
                        this.props.addToy(this.state.toy)
                        setSubmitting(false)
                    })
                    this.props.history.push('/toy')
                }}
            >
                {({ isSubmitting }) => (
                    <Form className="flex column justify-center align-center">
                        <h1>Add new Toy</h1>
                        <Field type="text" name="name" as={TextFieldOutlined} />
                        <ErrorMessage name="name" component="span" />
                        <Field type="number" name="price" as={TextFieldOutlined} />
                        <ErrorMessage name="price" component="span" />
                        <Field as="select" name="type">
                            <option value=""></option>
                            <option value="Adult">Adult</option>
                            <option value="Educational">Educational</option>
                            <option value="Funny">Funny</option>
                        </Field>

                        <label style={{ cursor: 'pointer' }}> Upload Photo!
                        <input onChange={(ev) => this.onUpdateImg(ev)} type="file" hidden />
                        </label>

                        <Button type="submit" disabled={isSubmitting}>
                            Submit
                        </Button>
                        <Button type="submit" onClick={() => this.props.history.goBack()}>
                            back
                        </Button>
                    </Form>
                )}
            </Formik>
        </div >)

    }

}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys

    }
}


const mapDispatchToProps = {
    loadToys,
    addToy,
    editToy
}

export const ToyEdit = connect(mapStateToProps, mapDispatchToProps)(_ToyEdit)