import React from 'react'
import { GoogleApiWrapper, Map, Marker } from 'google-maps-react';
import { Button } from '@material-ui/core';


// const style = {
//     width: '100%',
//     height: '50%'
// }
class _GoogleMap extends React.Component {

    state = {
        center: {},
        store1Coord: {
            lat: 32.0853,
            lng: 34.7818
        },
        store2Coord: {
            lat: 32.790509,
            lng: 34.967076
        },
        store3Coord: {
            lat: 33.004200,
            lng: 35.087510
        }
    }

    centerByStor = (storCoord) => {
        this.setState({ center: this.state[storCoord] })
    }


    onMarkerClick = (props, marker, event) => {
        console.log('props:', props, ' marker:', marker, 'event', event)
        const center = { lat: props.position.lat, lng: props.position.lng }
        this.setState({ center });
    }

    onMapClicked = (mapProps, map, ev) => {
        console.log('mapProps:', mapProps);
        console.log('map:', map);
        console.log('clickEvent:', ev);
        this.setState({ lat: ev.latLng.lat(), lng: ev.latLng.lng() })
    }

    render() {
        const containerStyle = {
        position: 'relative',
        width: '100%',
        height: '500px',
        border: '2px solid black',
        marginBottom: '10px',
        boxShadow: '15px 11px 9px 2px rgba(112,110,112,0.45)'
    }
        return (

            < div className = "map-container" >
                <Map containerStyle = { containerStyle } initialCenter={this.state.store2Coord} center={this.state.center} onClick={this.onMapClicked} google={this.props.google} zoom={8}>

                    <Marker position={this.state.store1Coord}
                        name={'store 1 location'} onClick={() => this.onMarkerClick} />
                    <Marker position={this.state.store2Coord}
                        name={'store 2 location'} onClick={() => this.onMarkerClick} />
                    <Marker position={this.state.store3Coord}
                        name={'store 3 location'} onClick={() => this.onMarkerClick} />

                    <div className="location-btns flex space-around" >
                        <Button variant="contained" color="primary" onClick={() => this.centerByStor('store2Coord')}>
                            Haifa Stor
                </Button>
                        <Button variant="contained" color="primary" onClick={() => this.centerByStor('store3Coord')}>
                            Naharya Stor
                </Button>
                        <Button variant="contained" color="primary" onClick={() => this.centerByStor('store1Coord')}>
                            TLV Stor
                </Button>
                    </div>

                    {/* <InfoWindow position={this.state} visible={true}  >
                    <div>
                        <h1>INFO WINDOW</h1>
                    </div>
                </InfoWindow> */}
                </Map>
            </div >
        );
    }
}

export const GoogleMap = GoogleApiWrapper({
    apiKey: ('AIzaSyA7wFxeGayDFtxLfft53sDr7sMu9cj7Vio')
})(_GoogleMap)