import React, { Component } from 'react'
import { MenuItem, TextField, Checkbox } from '@material-ui/core'

export class ToyFilter extends Component {
    state = {
        filter: {
            name: '',
            inStock: false,
            type: '',
            sortBy: ''
        }
    }

    handleChange = (ev) => {
        ev.persist()

        const value = (ev.target.type === 'checkbox') ? ev.target.checked : ev.target.value
        this.setState(prevState => ({ filter: { ...prevState.filter, [ev.target.name]: value } }), () => {
            this.props.onSetFilter(this.state.filter)

        })
        
    }
    onFilter = (ev) => {
        ev.preventDefault()
        this.props.onSetFilter(this.state.filter)
    }

    render() {
        const { name } = this.state.filter
        return (
            <form onSubmit={this.onFilter} className="form-filter flex space-evenely flex-1 align-center">
                <TextField type="text" name="name" label="By name" id="name" value={name} onChange={this.handleChange} />
                <label htmlFor="inStock">only in stock
                <Checkbox type="checkbox" name="inStock" id="inStock" color="primary" onChange={this.handleChange} />
                </label>
                <TextField id="select" label="type" style={{width : '60px'}} select>
                    <MenuItem value="" >All</MenuItem>
                    <MenuItem value="Educational">Educational</MenuItem>
                    <MenuItem value="Funny">Funny</MenuItem>
                    <MenuItem value="Adult">Adult</MenuItem>
                </TextField>
                {/* <select name="sort" id="" onChange={this.handleChange}>
                    <option value="">All</option>
                    <option value="name">name</option>
                    <option value="price">price</option>
                </select> */}
                <button>Filter</button>
            </form>
        )
    }
}
