import React from 'react'
import { ToyPreview } from './ToyPreview'

export  function ToyList({toys, onDeleteToy}) {
    return (
        <div className="toy-list flex flex-wrap justify-center">
            {
                toys.map(toy => <ToyPreview toy={toy} key= { toy._id } onDeleteToy={onDeleteToy}/>)
            }
        </div>
    )
}
