import React from 'react';
import { connect } from 'react-redux';
import { addReview } from '../store/actions/reviewActions';

class _ReviewAdd extends React.Component {

    state = {
        review: {
            toyId: '',
            // fullName: '',
            rate: 0,
            // date: '',
            content: ''
        }
    }


    elInput = React.createRef()

    componentDidMount() {
        // const { id } = this.props.toyId
        // toyService.getById(id).then(toy => this.setState({ toy }))
        this.setState({review : {toyId: this.props.toyId}})
        // this.elInput.current.focus()
    }

    onInputChange = (ev) => {
        this.setState(
            {
                review: { ...this.state.review, [ev.target.name]: ev.target.value }
            }
        )
    }

    submitReview = (ev) => {
        ev.preventDefault();
        this.props.addReview(this.state.review)
        
    }

    render() {
        return (
            <div>
                <form className="review-form flex column align-center" onSubmit={this.submitReview}>
                    <h4>rate this toy!</h4>
                    {/* <input ref={this.elInput} name="fullName" type="text" placeholder="Enter your full name" onChange={this.onInputChange} /> */}
                    <select name="rate" id="" className="rating-drop" onChange={this.onInputChange}>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                    </select>
                    {/* <input name="date" type="date" onChange={this.onInputChange} /> */}
                    <textarea name="content" id="" cols="30" rows="5" placeholder="write your review here" onChange={this.onInputChange}></textarea>
                    <button className="review-btn">submit</button>
                </form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {

    }
}

const mapDispatchToProps = {
    addReview
}

export const ReviewAdd = connect(mapStateToProps, mapDispatchToProps)(_ReviewAdd)