import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { AppBar, Toolbar } from '@material-ui/core';
import { connect } from 'react-redux';
import { logout } from '../store/actions/userActions';

function _Header(props) {


async function onLogout() {
    await props.logout()
    props.history.push('/')
}

    const { loggedinUser } = props
    return (
        // <header className="main-header full">
        <AppBar position="static" className="app-material-bar full main-container">
            <Toolbar className="main-nav flex space-between">
                <Link to="/" className="logo">MisterToy</Link>
                {loggedinUser && <h1>Hello {loggedinUser.username}!</h1>}
                <ul className="main-nav flex clean-list">
                    <Link to="/toy"><div className="logo">toys</div></Link>
                    <Link to="/about"><div className="logo">about</div></Link>
                    {/* <li className="link flex align-center">Link3</li> */}
                    {!loggedinUser && <Link to='/login'>Login</Link>}
                    {!loggedinUser && <Link to='/signup'>Signup</Link>}
                    
                    {loggedinUser && <button onClick={() => onLogout()}>Logout</button>}
                </ul>
            </Toolbar>
        </AppBar>

    )
}

const mapStateToProps = state => {
    return {
        loggedinUser: state.userReducer.loggedinUser
    }
}

const mapDispatchToProps = {
    logout
}

export const Header = connect(mapStateToProps, mapDispatchToProps)(withRouter(_Header))

