import Axios from 'axios'
const BASE_URL = process.env.NODE_ENV === 'production'
    ? '/api/toy'
    : '//localhost:3030/api/toy'


const axios = Axios.create({
    withCredentials: true
})

const resolveData = res => res.data

export const toyService = {
    query,
    getById,
    remove,
    save
}

function query(filterBy = {}) {
    
    var queryParams = new URLSearchParams()
    if (filterBy.name) queryParams.set('q', filterBy.name)
    if (filterBy.inStock) queryParams.set('inStock', filterBy.inStock)
    if (filterBy.type) queryParams.set('type', filterBy.type)
    if (filterBy.type) queryParams.set('sort', filterBy.sortBy)
    
    return axios.get(`${BASE_URL}?${queryParams}`)
        .then(resolveData)
}



function getById(toyId) {
    return axios.get(`${BASE_URL}/${toyId}`)
        .then(resolveData)
}


function remove(toyId) {
    return axios.delete(`${BASE_URL}/${toyId}`)
}

function save(toy) {
    if (toy._id) {
        return axios.put(`${BASE_URL}/${toy._id}`, toy)
    } else {
        return axios.post(BASE_URL, toy).then(resolveData)
    }
}
