import React from 'react';

import { connect } from 'react-redux'
import { signup } from '../store/actions/userActions'

class _Signup extends React.Component {
    state = {
        user: {
            username: '',
            fullName: '',
            password: ''
        }
    }
    onChange = (ev) => {
        this.setState({ user: { ...this.state.user, [ev.target.name]: ev.target.value } })
    }

    onSignup = async (ev) => {
        ev.preventDefault();
        const { username, fullName, password } = this.state.user;
        if (!username || !fullName || !password) return;
        const signupCreds = { username, fullName, password };
        await this.props.signup(signupCreds);
        this.setState({ user: { username: '', fullName: '', password: '' } }, () => this.props.history.push('/toy'));
        
    }

    render() {
        return (
            <form className="signup-form flex column align-center" onSubmit={this.onSignup}>
                <h1>Signup</h1>
                <input name="fullName" type="text" placeholder="full Name" onChange={this.onChange} />
                <input name="username" type="text" placeholder="User Name" onChange={this.onChange} />
                <input name="password" type="password" placeholder="Password" onChange={this.onChange} />
                <button>Signup</button>
            </form>
        )
    }
}


const mapStateToProps = state => {
    return {
        user: state.userReducer.loggedinUser,
    }
}
const mapDispatchToProps = {
    signup
}

export const Signup = connect(mapStateToProps, mapDispatchToProps)(_Signup)