import { Link } from 'react-router-dom'

import React, { Component } from 'react'
import { connect } from 'react-redux'
import { loadToys, removeToy, addToy } from '../store/actions/toyActions'
import { ToyList } from '../cmps/ToyList'
import { ToyFilter } from '../cmps/ToyFilter'


export class _ToyApp extends Component {

    state = {
        filterBy: '',
        sortBy: ''
    }

    componentDidMount() {
        this.props.loadToys()
    }

    onSetFilter = (filterBy) => {
        this.setState({ filterBy }, () => this.props.loadToys(this.state.filterBy))
    }

    onSetSort = (sortBy) => {
        this.setState({ sortBy }, () => this.props.loadToys(this.state.sortBy))
    }

    onDelete = (toyId) => {
        this.props.removeToy(toyId)
    }


    render() {
        const { toys, loggedinUser } = this.props
        if (!toys) return <div>Loading...</div>
        return (
            <div>
                <div className="add-filter flex align-center space-around">
                {loggedinUser && loggedinUser.isAdmin && <Link to="/toy/edit" className="add-btn">+</Link>}
                <ToyFilter onSetFilter={this.onSetFilter} />
                </div>
                {/* <IconButton aria-label="delete">
                 <Icon style={{ fontSize: 30} } backgrounColor="primary" >+</Icon>
                </IconButton> */}
                <ToyList toys={toys} onDeleteToy={this.onDelete} />
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys,
        loggedinUser: state.userReducer.loggedinUser
    }
}

const mapDispatchToProps = {
    loadToys,
    removeToy,
    addToy
}

export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp)