import { toyService } from "../../services/toyService"


export function loadToys(filterBy){
    return dispatch => {
        toyService.query(filterBy).then(toys => {
            dispatch({type: 'SET_TOYS', toys })
        })
    }
}

export function addToy(toy) {
    return dispatch => {
        toyService.save(toy)
            .then((toy) => {
                dispatch({ type: 'ADD_TOY', toy })
                dispatch({ type: 'NOTIFY', isShown: true, msg: 'Added seccessfuly'})
                
            })
    }
}

export function editToy(toy) {
    return dispatch => {
        toyService.save(toy)
            .then((toy) => {
                dispatch({ type: 'EDIT_TOY', toy })
                dispatch({ type: 'NOTIFY', isShown: true, msg: 'Added seccessfuly'})
                
            })
    }
}

export function removeToy(id) {
    return dispatch => {
        toyService.remove(id)
            .then(() => {
                dispatch({ type: 'REMOVE_TOY', id })
                dispatch({ type: 'NOTIFY', isShown: true, msg: 'Removed seccessfuly'})
            })
    }
}