
import React from 'react';
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';

export function _ToyPreview(props) {
    const { toy, loggedinUser } = props

    return (
        <div className="toy-preview card flex space-around column align-center">
            <img src={toy.imgUrl ? toy.imgUrl : require(`../assets/img/default.jpg`)} alt="" />
            {toy.name}
            <div className="edit-toy-btns flex">
                <Link to={`/toy/${toy._id}`}>Details</Link>
                {loggedinUser && loggedinUser.isAdmin && <Link to={`/toy/edit/${toy._id}`}>Edit</Link>}
                {loggedinUser && loggedinUser.isAdmin && <button onClick={() => props.onDeleteToy(toy._id)}>X</button>}
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        loggedinUser: state.userReducer.loggedinUser
    }
}



export const ToyPreview = connect(mapStateToProps)(_ToyPreview)

