const initialState = {
    toys: []
}

export function toyReducer(state = initialState, action) {
    switch (action.type) {
        case 'SET_TOYS':
            return {
                ...state,
                toys: action.toys
            }
        case 'REMOVE_TOY':
            const toys = state.toys.filter(toy => toy._id !== action.id)
            return { ...state, toys }
        case 'ADD_TOY':
            let newToys = state.toys.slice()
            newToys.push(action.toy)
            return { ...state, toys: newToys }
        case 'EDIT_TOY':
            {
                const toyIdx = state.toys.findIndex((toy) => toy._id === action.toy._id)
                const updateToys = state.toys.slice()
                updateToys[toyIdx] = action.toy
                return { ...state, toys: updateToys }
            }
        default:
            return state
    }
}