import React, { Component } from 'react'
import { Pie, Line } from 'react-chartjs-2';
import { connect } from 'react-redux'
import { loadToys, addToy } from '../store/actions/toyActions'


export class _Home extends Component {

    componentDidMount() {
        this.props.loadToys()
    }

    getToysDataByType = () => {
        let chartByType = this.props.toys.reduce((acc, toy) => {
            if (toy.type === 'Adult') acc[0] += +toy.price;
            else if (toy.type === 'Educational') acc[1] += +toy.price;
            else if (toy.type === 'Funny') acc[2] += +toy.price;
            return acc
        }, [0, 0, 0])
        return chartByType;
    }

    getToysDataByYear = () => {
        const mapByYear = this.props.toys.reduce((acc, toy) => {
            let toyCreationDate = new Date(toy.createdAt)
            let toyCreationYear = toyCreationDate.getFullYear()
            acc[toyCreationYear] = acc[toyCreationYear] ? acc[toyCreationYear] + 1 : 1
            return acc
        }, {})

        let years = Object.keys(mapByYear)
        let values = Object.values(mapByYear)

        return { years, values }
    }



    render() {
        const { toys } = this.props
        if (!toys.length) return <h1>loading</h1>
        const dataPrice = {
            labels: ['Adult', 'Educational', 'Funny'],
            datasets: [
                {
                    label: 'Sales per year',
                    backgroundColor: ['#b2f0d4',
                        '#2d30b4',
                        '#88ccd9'],
                    borderColor: 'rgba(255,99,132,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: '#6ceff0',
                    data: this.getToysDataByType()
                }
            ]
        };
        const toysByYear = this.getToysDataByYear()
        const dataYear = {
            labels: toysByYear.years,
            datasets: [
                {
                    label: 'Sales per year',
                    backgroundColor: 'lightBlue',
                    borderColor: 'rgba(255,99,132,1)',
                    borderWidth: 1,
                    hoverBackgroundColor: 'rgba(255,99,132,0.4)',
                    hoverBorderColor: 'rgba(255,99,132,1)',
                    data: toysByYear.values
                }
            ]
        };

        return (
            <React.Fragment>
                <div className="welcome-div flex column">
                    <div className="welcome-txt flex column justify-center">
                    <h1>Welcome to <span>MisterToy</span></h1>
                    <p>our toys are the best on the planet! Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptatum atque dolores optio saepe repellendus, corporis, possimus quod nemo dicta ratione explicabo unde? Minima, nulla ea! Ut vitae repudiandae reiciendis natus.</p>

                    </div>
                    {/* <img src={require('../assets/img/woody-img.png')} alt=""/> */}
                </div>
                <div className="chart-container flex flex-wrap">
                    <Pie data={dataPrice} />
                    <Line data={dataYear} />
                </div>

            </React.Fragment>
        )
    }
}

const mapStateToProps = state => {
    return {
        toys: state.toyReducer.toys
    }
}

const mapDispatchToProps = {
    loadToys,
    addToy
}

export const Home = connect(mapStateToProps, mapDispatchToProps)(_Home)


