import React, { Component } from 'react';
import socketService from '../services/socketService';


export default class ChatRoom extends Component {

    state = {
        msg: { from: 'Me', txt: '' },
        msgs: [],
        topic: '',
        isTyping: false,
        typingMsg: ''
    };

    componentDidMount() {
        this.setState({ topic: this.props.toyId, msg: { from: this.props.username, txt: '' } })
        socketService.setup();
        socketService.emit('chat topic', this.state.topic);
        socketService.on('chat addMsg', this.addMsg);
        socketService.on('chat typing', this.onTyping);
    }

    componentWillUnmount() {
        socketService.off('chat addMsg', this.addMsg);
        socketService.terminate();
    }

    onTyping = typingMsg => {
        this.setState({ typingMsg })
    }

    addMsg = newMsg => {
        this.setState(prevState => ({ msgs: [...prevState.msgs, newMsg] }));
    };

    //   changeTopic = () => {
    //     socketService.emit('chat topic', this.state.topic);
    //   };

    sendMsg = ev => {
        ev.preventDefault();
        socketService.emit('chat newMsg', this.state.msg);
        this.setState({ msg: { from: this.props.username, txt: '' } });
    };

    //   handleChange = ev => {
    //     const { name, value } = ev.target;
    //     this.setState({ [name]: value }, () => this.changeTopic(value));
    //   };

    // msgHandleChange = ev => {
    //     const { name, value } = ev.target;
    //     this.setState(prevState => {
    //         return {
    //             msg: {
    //                 ...prevState.msg,
    //                 [name]: value
    //             },
    //             isTyping: true
    //         };
    //     });
    // };

    timeoutFunction = () => {
        this.setState({ isTyping: false });
        socketService.emit('chat typing', '');
    }

    msgHandleChange = async ev => {
        var timeout;
        if (!this.state.isTyping) {
            clearTimeout(timeout);
            this.setState({ isTyping: true });
            const userName = this.props.username;
            const typingMsg = userName + ' is typing...';
            socketService.emit('chat typing', typingMsg);
            timeout = setTimeout(this.timeoutFunction, 1000);
        } else {
            clearTimeout(timeout);
            timeout = setTimeout(this.timeoutFunction, 1000);
        }
        const { name, value } = ev.target;
        await this.setState(prevState => {
            return {
                msg: {
                    ...prevState.msg,
                    [name]: value,
                }
            };
        });
    };

    render() {
        return (
            <div className="chat-room flex column space-between align-center">
                {/* <h1>About Us</h1> */}
                {/* <p>We like You</p> */}
                <h2>Chat about this toy!</h2>
                <div style={{ height: '30px', lineHeight: 0 }}>
                    {this.state.typingMsg && <h3>{this.state.typingMsg}</h3>}
                </div>
                <ul className="clean-list flex column">
                    {this.state.msgs.map((msg, idx) => (
                        <li key={idx}>{`${msg.from}: ${msg.txt}`}</li>
                    ))}
                </ul>
                <form className="flex" onSubmit={this.sendMsg}>
                    <input
                        autoComplete="off"
                        type="text"
                        value={this.state.msg.txt}
                        onChange={this.msgHandleChange}
                        name="txt"
                    />
                    <button>Send</button>
                </form>

            </div>
        );
    }
}
