import React from 'react'


export function ReviewPreview(props) {
    if (!props.review) return 'Be the first one to give a review!'
    return (
        <ul className="review-container">
            <li className="flex justify-center column align-center">
                <h2>{'‚≠ê'.repeat(props.review.rate)}</h2>
                <p>"{props.review.content}"</p>
            </li>
        </ul>
    )
}